<template>
  <ec-container height="100%">
    <ec-container-header>
      <ec-panel-item>
        <ec-panel-item-left>
          <ec-header title="卷烟生产退料码包分配领用" icon="onetomany" color-code="008" />
        </ec-panel-item-left>
      </ec-panel-item>
    </ec-container-header>
    <ec-container-body>
      <ec-panel :auto="false">
        <ec-panel-toolbar>
          <a-descriptions bordered :column="4">
            <a-descriptions-item label="调拨年度">
              {{ visibleTranYear }}
            </a-descriptions-item>
            <a-descriptions-item label="烟叶类型"> {{ this.leafTypeName }}</a-descriptions-item>
            <a-descriptions-item label="烟叶形态">
              {{ totalData.leafFormName }}
            </a-descriptions-item>
            <a-descriptions-item label="申请码包数量">
              {{ totalData.applyNum }}
            </a-descriptions-item>

            <a-descriptions-item label="申请量">
              {{ totalData.applyWeight }}
            </a-descriptions-item>
            <a-descriptions-item label="分配码包数量">
              {{ totalData.allotNum }}
            </a-descriptions-item>
            <a-descriptions-item label="领用码包数量">
              {{ totalData.collectNum }}
            </a-descriptions-item>
            <a-descriptions-item label=""> </a-descriptions-item>
          </a-descriptions>
        </ec-panel-toolbar>

        <ec-panel-toolbar>
          <ec-query-panel :defaultFold="true" @query="$refs.table.pageAction(1)" @reset="onReset">
            <ec-query-item label="调拨年度">
              <a-input-number v-model="queryItems.tranYear" :min="2000" :max="2099" />
            </ec-query-item>
            <ec-query-item label="领用单位">
              <select-picker
                title="选择领用单位"
                :lazy="false"
                :search="false"
                :fetchData="onFetchBusinessOrgList"
                v-model="queryItems.applyOrgCode"
                @ok="handleStore()"
              />
            </ec-query-item>
            <ec-query-item label="仓库">
              <ec-select placeholder="请选择" v-model="queryItems.storeCode">
                <a-select-option v-for="storeObj in storeList" :key="storeObj.storeCode">
                  {{ storeObj.storeName }}
                </a-select-option>
              </ec-select>
            </ec-query-item>
            <!-- 本级及下级单位 -->

            <template #inner>
              <ec-query-item label="烟叶类型">
                <ec-select
                  showSearch
                  allowClear
                  placeholder="请选择"
                  v-model="queryItems.leafTypeCode"
                  @select="handleLeafType"
                >
                  <a-select-option
                    v-for="leafTypeEk in leafTypeList"
                    :key="leafTypeEk.leafTypeCode"
                  >
                    {{ leafTypeEk.leafTypeName }}
                  </a-select-option>
                </ec-select>
              </ec-query-item>
              <ec-query-item label="烟叶形态">
                <ec-select
                  showSearch
                  allowClear
                  placeholder="请选择"
                  v-model="queryItems.leafFormCode"
                >
                  <a-select-option
                    v-for="item in leafFormList"
                    :key="item.leafFormCode"
                    :value="item.leafFormCode"
                    >{{ item.leafFormName }}</a-select-option
                  >
                </ec-select>
              </ec-query-item>
            </template>
          </ec-query-panel>
          <ec-divider />
        </ec-panel-toolbar>
        <ec-panel-header>
          <ec-panel-item>
            <ec-panel-item-right>
              <a-space :size="15">
                <span
                  >重量单位：<ec-select
                    showSearch
                    allowClear
                    placeholder="请选择"
                    v-model="weightUnit"
                    @select="handleUnitParse"
                    style="width: 150px"
                  >
                    <a-select-option
                      v-for="weightUnit in weightUnitList"
                      :key="weightUnit.weightUnitCd"
                    >
                      {{ weightUnit.weightUnitName }}
                    </a-select-option>
                  </ec-select>
                </span>
              </a-space>
            </ec-panel-item-right>
          </ec-panel-item>
        </ec-panel-header>
        <ec-panel-body height="100%">
          <ec-table
            ref="table"
            border
            height="100%"
            :auto="false"
            @page-action="handlePagination"
            :pagination="pagination"
            :data="tableData.rows"
            :loading="loading"
            highlight-current-row
          >
            <ec-table-col type="checkbox" width="50" align="center"></ec-table-col>
            <ec-table-col title="序号" type="seq" width="70" align="center" />
            <ec-table-col field="tranYear" title="调拨年度" align="center" width="150" />
            <ec-table-col field="applyOrgName" title="领用单位" align="left" min-width="180" />
            <ec-table-col field="storeName" title="仓库" align="left" min-width="180" />
            <ec-table-col field="leafTypeName" title="烟叶类型" align="left" width="150" />
            <ec-table-col field="leafFormName" title="烟叶形态" align="left" width="150" />
            <ec-table-col field="applyNum" title="申请码包数量" align="right" width="150" />
            <ec-table-col field="applyWeight" title="申请量" align="right" width="150" />
            <ec-table-col field="allotNum" title="分配码包数量" align="right" width="150" />
            <ec-table-col field="collectNum" title="领用码包数量" align="right" width="150" />
            <ec-table-col title="操作" align="center" width="150" fixed="right">
              <template #default="{ row }">
                <a-button type="link" @click="onDetail(row)">详细</a-button>
              </template>
            </ec-table-col>
          </ec-table>
        </ec-panel-body>
      </ec-panel>
      <receive-detail ref="ReceiveDetail"></receive-detail>
    </ec-container-body>
  </ec-container>
</template>

<script>
import ReceiveDetail from './ReceiveDetail.vue';
import { SelectPicker, OrgPicker } from '@euler-material/ecm-ui';
import { treeFormatList } from '@euler-material/ecm-utils';
import _ from 'lodash';

export default {
  name: 'ExecutionManage',
  components: {
    ReceiveDetail,
    SelectPicker,
    OrgPicker,
  },
  data() {
    return {
      objectData: {},
      busiYear: '',
      queryItems: {
        tranYear: '',
        applyOrgCode: undefined,
        storeCode: undefined,
        leafTypeCode: undefined,
        leafFormCode: undefined,
      },
      defaultLeafFormCode: '',
      weightUnit: 1,
      areaUnit: 10000,
      nowYear: '',
      loading: false,
      leafTypeCd: '',
      leafTypeList: [],
      oldLeafTypeList: [],
      oldDataRow: [], // 拷贝的旧数据，单位切换的时候要在旧的数据上转换
      oldTotalData: [], // 拷贝的旧上级数据，单位切换的时候要在旧的数据上转换
      totalData: {}, // 合计的数据
      isNullData: [],
      leafTypeName: '',
      beforeLeafTypeName: '',

      weightUnitList: [
        { weightUnitCd: 1, weightUnitName: '公斤', isSelected: false },
        { weightUnitCd: 50, weightUnitName: '担', isSelected: false },
        // { weightUnitCd: 500000, weightUnitName: '万担', isSelected: false },
      ],

      visibleTranYear: '',
      storeList: [],
      orgCodeList: [],
      procOrgList: [],
      enumMap: {},
      packTypeList: [],
      leafFormList: [],
      params: {},
      // 商业单位
      businessOrgList: [],
      bizCode: '',
      // 组织字段重置
      replaceFields: {
        pkey: 'parentBizCode',
        title: 'orgName',
        key: 'bizCode',
        children: 'children',
      },
      pagination: {
        pageNumber: 1,
        pageSize: 10,
        total: 0,
      },
      tableData: {
        // 数据表格
        // totalRows: 0,
        rows: [],
      },
    };
  },
  computed: {
    appConfig() {
      return this.$store.getters.getAppConfig;
    },
  },
  async created() {
    const { appCode } = this.$router.history.current.query;
    this.appCode = appCode || 'CM'; // 获取当前登入信息

    // 包装类型
    const enumTypeCodes = ['PC_PACK_TYPE'];
    const data = {
      enumTypeCodes,
    };
    const res = await this.$apis.enum.index.getEnumType(data);
    const enumMap = res?.enumMap || {};
    const packTypeList = enumMap?.PC_PACK_TYPE || [];

    this.packTypeList = packTypeList;

    this.bizCode = this.appConfig?.bizCode;
    this.orgCode = this.appConfig?.orgCode; // todo 写死数据
    this.orgName = this.appConfig?.orgName;
    this.queryItems.tranYear = this.appConfig?.yearValueMap?.SM_YEAR;
    this.nowYear = this.appConfig?.yearValueMap?.SM_YEAR;
    this.visibleTranYear = this.appConfig?.yearValueMap?.SM_YEAR;

    await this.handleQueryBusinessOrgList();

    this.queryItems.applyOrgCode = this.bizCode;
    this.handleStore();
  },
  methods: {
    async handleLeafType() {
      this.beforeLeafTypeName = this.handleLeafTypeName(this.queryItems.leafTypeCode);
      await this.$apis.pcm.penatly.apply
        .spinnerLeafForm({ leafTypeCodes: [this.queryItems.leafTypeCode] })
        .then((info) => {
          this.leafFormList = info?.rows;
          this.leafFormList.some((item) => {
            if (item.leafFormCode === '101') {
              // this.leafTypeCd = item.leafTypeCode;
              this.queryItems.leafFormCode = item.leafFormCode;
              console.log(this.queryItems.leafFormCode);
              return true;
            } else {
              return false;
            }
          });
        });
    },
    // 查询商业单位
    async handleQueryBusinessOrgList() {
      const params = {
        busiYear: this.nowYear,
        orgTypeEk: 'TOBACCO',
        orgLevelEk: '6',
        parentBizCode: this.bizCode,
      };
      const res = await this.$apis.common.index.queryTreeByCode(params);
      this.businessOrgList = res.rows || [];
    },
    // 更新商业单位
    onFetchBusinessOrgList() {
      return new Promise((resolve) => {
        const data = treeFormatList(this.businessOrgList, this.replaceFields);
        resolve(data);
      });
    },

    async handleStore() {
      this.queryItems.storeCode = undefined;
      const data = {
        bizCode: this.queryItems.applyOrgCode,
        busiYear: this.queryItems.tranYear,
      };
      const storeList = await this.$apis.common.index.queryStore(data);
      const storeRows = storeList?.rows || [];
      if (storeRows && storeRows.length > 0) {
        storeRows.unshift({ storeName: '全部', storeCode: '' });
        this.queryItems.storeCode = storeRows[0]?.storeCode;
      }
      this.storeList = storeRows;
    },
    // 处理单位转换
    handleUnitParse() {
      //  列表的单位转换    totalWeight     leftWeight   applyWeight
      const handleOlDataRow = JSON.parse(JSON.stringify(this.oldDataRow));
      handleOlDataRow.forEach((item) => {
        const itemObj = item;
        itemObj.totalWeight = (item.totalWeight / this.weightUnit).toFixed(2);
        itemObj.leftWeight = (item.leftWeight / this.weightUnit).toFixed(2);
        itemObj.applyWeight = (item.applyWeight / this.weightUnit).toFixed(2);
      });

      const handleOldTotalData = JSON.parse(JSON.stringify(this.oldTotalData));

      // 汇总数据
      handleOldTotalData.totalWeight = (handleOldTotalData.totalWeight / this.weightUnit).toFixed(
        2,
      );
      handleOldTotalData.leftWeight = (handleOldTotalData.leftWeight / this.weightUnit).toFixed(2);
      handleOldTotalData.applyWeight = (handleOldTotalData.applyWeight / this.weightUnit).toFixed(
        2,
      );

      this.totalData = handleOldTotalData;
      this.tableData.rows = handleOlDataRow;
    },

    async handlePagination(pageNumber, pageSize) {
      this.pagination.pageNumber = pageNumber;
      this.pagination.pageSize = pageSize;
      await this.handleGetLeafTypeList();
    },

    // 获取烟叶类型数据
    async handleGetLeafTypeList() {
      if (!this.leafTypeList?.length) {
        const res = await this.$apis.common.index.queryLeafType({});
        const rows = res?.rows || [];
        this.oldLeafTypeList = _.cloneDeep(rows);
        if (rows && rows.length > 0) {
          rows.unshift({ leafTypeName: '全部', leafTypeCode: '' });
        }
        this.leafTypeList = rows;
        this.leafTypeList.some((item) => {
          if (item.leafTypeName === '烤烟') {
            this.leafTypeCd = item.leafTypeCode;
            this.queryItems.leafTypeCode = item.leafTypeCode;
            this.leafTypeName = '烤烟';
            return true;
          } else {
            return false;
          }
        });

        await this.handleLeafType();
      }
      if (!this.queryItems.leafTypeCode && this.leafTypeList?.length) {
        const { leafTypeCode } = this.leafTypeList[0];
        this.leafTypeCd = leafTypeCode;
        this.queryItems.leafTypeCode = leafTypeCode;

        this.leafTypeName = this.leafTypeList[0].leafTypeName;
      }

      // if (!this.orgCode) {
      //   const orgObj = await this.$apis.bim.curOrg.get({});
      //   console.log(`当前用户信息：${JSON.stringify(orgObj)}`);
      //   this.bizCode = orgObj?.bizCode;
      //   this.orgCode = orgObj?.orgUniqueCode; // todo 写死数据
      //   this.orgName = orgObj?.orgName;
      //   this.queryItems.applyOrgCode = this.bizCode;

      //   this.orgCodeList = [this.orgCode];
      // }
      // if (!this.queryItems.tranYear > 0) {
      //   // 获取年度
      //   const data = {
      //     bizCode: this.appConfig.bizCode, // todo 写死的数据
      //     appCode: this.appCode || 'CM',
      //     yearTypeCode: 'SM_YEAR',
      //   };
      //   const busiYearObj = await this.$apis.bpm.executionEnter.getBuYear(data);
      //   this.queryItems.tranYear = busiYearObj?.yearValue;
      //   this.nowYear = busiYearObj?.yearValue;
      //   this.visibleTranYear = busiYearObj?.yearValue;

      //   this.handleStore();
      // }

      // if (!this.businessOrgList?.length) {
      //   await this.handleQueryBusinessOrgList();
      // }
      this.onQuery();
    },

    // 数据初始化
    async onQuery() {
      this.loading = true;
      this.leafTypeName = this.beforeLeafTypeName || this.leafTypeName;

      this.visibleTranYear = this.queryItems.tranYear;
      const data = {
        applyType: 'BACK_CP',
        pageNo: this.pagination.pageNumber,
        pageSize: this.pagination.pageSize,
        ...this.queryItems,
      };

      console.log(JSON.stringify(data));
      await this.$apis.pcm.penatly.apply
        .pageConfCp(data)
        .then((res) => {
          const rows = res.rows || [];
          this.pagination.total = res.totalRows;
          this.oldDataRow = JSON.parse(JSON.stringify(rows));
          // this.tableData.rows = rows;
        })
        .catch((err) => {
          console.log(err);
        })
        .finally(() => {
          this.loading = false;
          this.selectedRows = [];
        });

      this.params = data;
      const totalData = await this.$apis.pcm.penatly.apply.queryHead(data);
      console.log(`汇总数据：${JSON.stringify(totalData)}`);
      this.oldTotalData = JSON.parse(JSON.stringify(totalData));
      this.handleUnitParse();
    },

    // 详细
    async onDetail(row) {
      const { params } = this;
      params.applyOrgCode = row.applyOrgCode;
      const detailData = await this.$apis.pcm.penatly.apply.queryDetail(params);
      const weightUnitObj = this.weightUnitList.find((res) => res.weightUnitCd === this.weightUnit);
      // 处理详细里面的申请量单位
      detailData.forEach((item) => {
        const itemObj = item;
        itemObj.applyWeight = (item.applyWeight / this.weightUnit).toFixed(2);
      });
      const data = {
        tableData: detailData,
        packTypeList: this.packTypeList,
        selectedRow: row,
        weightUnitObj,
      };
      this.$refs.ReceiveDetail.show(data);
    },

    // 重置
    async onReset() {
      this.queryItems.tranYear = this.nowYear;
      this.queryItems.leafTypeCode = this.leafTypeCd;
      this.queryItems.storeCode = undefined;
      this.queryItems.applyOrgCode = this.bizCode;
      await this.handleLeafType();
      this.handleStore();
      this.beforeLeafTypeName = this.handleLeafTypeName(this.leafTypeCd);
      this.$refs.table.pageAction(1);
    },

    // 根据编码翻译对应的名称
    handleLeafTypeName(typeCd) {
      const leafType = this.leafTypeList.find((item) => item.leafTypeCode === typeCd);
      return leafType?.leafTypeName;
    },

    // 选中所有列表
    selectAllEvent({ checked, records }) {
      console.log(`${checked}-选中所有records包含选中的所以对象----${JSON.stringify(records)}`);
      this.selectedRows = records;
    },

    // 选中列表
    selectRowChange({ checked, records }) {
      console.log(
        `${checked}-选择改变时会把records包含选中的所以对象----${JSON.stringify(records)}`,
      );
      this.selectedRows = records;
    },
  },
};
</script>
<style lang="less" scoped>
.year {
  font-size: 18px;
  line-height: 22px;

  color: #18191a;
  font-weight: 700;
  padding-top: 18px;
  padding-right: 50px;
  font-family: '微软雅黑 Bold', '微软雅黑', 'Microsoft YaHei', sans-serif;
}
.paddingTop {
  padding-top: 15px;
}
</style>
